.header {
    padding-top: 9px;
    padding-bottom: 50px;

    &.--bordered {
        border: 1px solid #000000;
        padding-bottom: 17px;
    }

    .header__wrapper {
        display: flex;
        justify-content: space-between;
    }

    .header__logo {
        margin-right: 50px;
    }

    .header__left {
        display: flex;
        justify-content: start;
        align-items: center;
    }

    .header__menu {}

    .header__menu-inner {
        display: flex;
        justify-content: start;
        align-items: center;
    }

    .header__menu-item {
        font-family: 'Euclid Square';
        font-weight: 500;

        &:not(:last-child) {
            margin-right: 50px;
        }

        &.--mob {
            display: none;
        }
    }

    .header__search-btn {}

    .header__burger {
        margin-left: 27px;
        display: none;
        cursor: pointer;
    }

    .header__burger-normal {}

    .header__burger-close {
        display: none;
    }

    .header__lang {
        margin-left: 58px;
        font-weight: 500;
    }

    .header__search {
        display: none;
        position: absolute;
        top: 100%;
        left: 0;
        background: #fff;
        z-index: 100;
        width: 100%;
        cursor: pointer;

        padding: 65px 51px 56px;

        .container {
            position: relative;
        }
    }
    .header__search-close {
        position: absolute;
        top: -70px;
        right: 0;
    }
    .header__search-form {
        display: flex;
        justify-content: start;
        align-items: center;
    }
    .header__search-input {
        border: none;
        outline: none;
        padding: 0;
        padding-bottom: 8px;
        font-family: 'Euclid Square';
        font-weight: 500;
        font-size: 24px;
        line-height: 100%;
        letter-spacing: 0%;
        color: #0a1e3c;
        width: 100%;
        margin-right: 84px;
        border-bottom: 1px solid #000000;
    }
    .header__search-input::placeholder {
        color: #0a1e3c;
    }
    .header__search-submit {
        max-width: 227px;
        padding: 9px;
        flex-shrink: 0;
    }

    position: relative;
}

@media screen and (max-width: 1350px) {

    .header .header__lang,
    .header .header__menu {
        display: none;
    }


    .header .header__menu {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        z-index: 100;
        background: #fff;
    }

    .header__burger.is-active {
        .header__burger-normal {
            display: none;
        }

        .header__burger-close {
            display: block;
        }
    }

    .header .header__menu-inner {
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: start;
        padding: 35px 91px 164px 94px;
    }

    .header .header__menu-item:first-child {
        color: #0069d2;
    }

    .header .header__menu-item:not(:last-child) {
        margin-bottom: 15px;
        margin-right: 0;
    }

    .header .header__menu-item {
        font-size: 20px;
    }

    .header .header__burger {
        display: block;
    }

    .header__search-btn {
        width: 19px;
        height: 21px;
    }

    .header__search-btn svg {
        width: 19px;
        height: 21px;
    }
}

@media screen and (max-width: 768px) {
    .header .header__search-submit {
        display: none;
    }
    .header .header__search {
        padding-bottom: 93px;
    }
    .header .header__search-input {
        margin-right: 0;
    }
    .header .header__search {
        padding-left: 16px;
        padding-right: 16px;
    }
}