.footer {
    a {
        color: inherit;
    }
    padding-bottom: 9px;
}

.footer__wrapper {
    background: #0A1E3C;
    color: #fff;
    padding: 49px 106px 100px;
    border-radius: 0 75px 0 75px;
    clip-path: polygon(5% 0, 100% 0, 100% 95%, 95% 100%, 0 100%, 0 5%);
}

.footer__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 28px;
}

.footer__logo {}

.footer__arrow {}

.footer__main {
    padding: 45px 0;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    display: grid;
    grid-template-columns: 1fr 1.3fr 1fr 1fr;
    gap: 88px;
}

.footer__col {}

.footer__col-header {
    font-weight: 600;
    margin-bottom: 15px;
}

.footer__col-text {
    display: block;

    &:not(:last-child) {
        margin-bottom: 10px;
    }

    a {
        text-decoration: underline;
    }
}

.footer__socials {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
    margin: 20px 0;
}

.footer__social {}

.footer__bottom {
    padding-top: 51px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer__bottom-item {
    font-size: 12px;
}

.footer__partners {
    padding: 32px 0 50px;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #fff;
}

.footer__list {
    display: flex;
    justify-content: space-between;
    align-items: start;
    margin-top: 6px;
}

.footer__item {
    display: flex;
    justify-content: start;
    align-items: start;

    img {
        width: 24px;
        height: 24px;
        object-fit: cover;
        border-radius: 50%;
        flex-shrink: 0;
        object-fit: contain;
    }

    span {
        margin-left: 11px;
        font-size: 14px;
    }
}

.footer__all {
    display: block;
    margin: 0 auto;
    font-weight: 500;
    text-decoration: underline;
    margin-top: 40px;
}


.fancybox-slide--html .fancybox-close-small {
    display: none;
}

.partners-popup {
    max-width: 1338px !important;
    width: 100%;
    background: #0A1E3C;
    color: #fff;
    border-radius: 50px;
    display: none;
    padding: 0 !important;


    .partners-popup__inner {
        padding: 65px 117px 117px;
        position: relative;
    }

    .footer__arrow {}

    .partners-popup-close {
        position: absolute;
        top: 65px;
        right: 47px;
        filter: grayscale(1) brightness(100);
        cursor: pointer;
    }

    .partners-popup__body {}

    .footer__partners {
        border-top: 1px solid #fff;
    }

    .footer__col-header {}

    .footer__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        gap: 16px;
    }

    .footer__item {
        color: #fff;
    }
}

@media screen and (max-width: 1350px) {
    .footer .footer__wrapper {
        padding: 30px 48px;
        clip-path: polygon(50px 0, 100% 0, 100% 96%, 90% 100%, 0 100%, 0 50px);
    }

    .footer .footer__main {
        grid-template-columns: 1fr;
        gap: 40px;
        padding: 30px 0;
    }

    .footer .footer__item {
        span {
            display: none;
        }

        img {
            width: 40px;
            height: 40px;
        }
    }

    .footer .footer__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 40px;

    }

    .footer .footer__bottom {
        display: flex;
        flex-direction: column;
        align-items: start;
    }

    .footer .footer__bottom-item:not(:last-child) {
        margin-bottom: 10px;
    }

    .footer .footer__bottom {
        padding-top: 33px;
    }

    .footer .footer__partners {
        padding: 30px 0;
    }


    .partners-popup .footer__list {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}

@media screen and (max-width: 768px) {
    .partners-popup .partners-popup__inner {
        padding: 28px 48px 79px;
    }

    .partners-popup .partners-popup-close {
        top: 33px;
    }
    .partners-popup .footer__arrow {
        margin-right: 60px;
    }
    .partners-popup .footer__list {
        grid-template-columns: 1fr 1fr;
    }
}